import React from "react"
import classnames from "classnames"
import styled from "styled-components"
import { Popover } from "src/core/components"
import { HUtils, MyUI } from "@macashipo/mlib"
import useConfigTableCell from "../../hooks/useConfigTableCell"

const WrapAction = styled.div`
  padding: 0rem 0px;
  position: absolute;
  top: 2px;
  right: 2px;
  background: #f1f4f6;
  padding: 0px 5px;
  border: 1px solid #dee1e4;
  & > button {
    border: 0px;
    outline: none;
    background: transparent;
  }
`

function MTCDeletePopover(props) {
  const { ctc, row, title, msg } = props
  return (
    <div style={{ width: "250px", maxHeight: "300px", maxWidth: "300px" }}>
      <MyUI
        type={"msgbox_confirm"}
        title={title}
        msg={msg}
        colorOK="danger"
        onOK={() => {
          console.warn("onOK", props)
          HUtils.runFuntion(ctc.oneTableCell.fnList, "fnRequestDelete", [
            {
              row,
              cbSuccess: () => {
                HUtils.runFuntion(ctc.oneTableCell.fnList, "fnDeleteItem", [
                  row,
                ])
              },
            },
          ])
          if (props.hide) {
            props.hide()
          }
        }}
        onCancel={() => {
          console.warn("onCancel")
          if (props.hide) {
            props.hide()
          }
        }}
      />
    </div>
  )
}

export default function MUIWrapEditRowInCard(props) {
  const { style, className } = props
  const ctc = useConfigTableCell({
    props: {
      cell: "",
      row: "",
      extra: {
        fieldName: "",
      },
    },
  })
  let _canEdit = true
  let _canDelete = true
  return (
    <WrapAction className={className} style={style}>
      {_canEdit && (
        <button title="Edit">
          <i className="fa fa-edit" style={{ color: "#033c73" }} />
        </button>
      )}
      {_canDelete && (
        <Popover
          overlay={propsOverlay => {
            return (
              <MTCDeletePopover
                ctc={ctc}
                {...props}
                {...propsOverlay}
                title={"Delete"}
                msg={"Delete"}
              />
            )
          }}
          overlayClassName="m-popover"
          placement="topRight"
          destroyTooltipOnHide={true}
        >
          <button title="Delete">
            <i className="fa fa-trash" style={{ color: "#dc3545" }} />
          </button>
        </Popover>
      )}
    </WrapAction>
  )
}
